function Scroll() {
  let leftSide = document.querySelector('.employees');

  leftSide.addEventListener('wheel', (event) => {
    console.log(document.querySelector('.employees').offsetWidth);
    console.log(event.deltaY + ' y');
    console.log(event.deltaX + ' x');
    event.preventDefault();
    let element = document.querySelector('#selected-letter > div');

    if (element && element.clientHeight < element.scrollHeight) {
      element.scrollTop += event.deltaY;
      return;
    }

    leftSide.scrollLeft +=
      Math.max(event.deltaY, event.deltaX) ||
      Math.min(event.deltaY, event.deltaX);
  });
}

export default Scroll;
